import React, { useState } from 'react'
import '../../Global-Style.css';
import './About.css'
import image2 from '../Assets/image2.png'
import image3 from '../Assets/image3.png'
import image4 from '../Assets/image4.png'

const About = () => {
  const [data, setData] = useState({ name: '', email: '', phone: '' })
  const [show, setShow] = useState(false);
  const handleChange = (e) => {
    const name = e.target.name;
    const value = e.target.value;
    setData({ ...data, [name]: value })
  }

  const handleSubmit = (e) => {
    e.preventDefault();
    if (setShow(true)) {
      setShow(false);
    } else if (!data.name | !data.email | !data.phone) {
      alert('   ,    ');
    } else {
      setTimeout(() => {
        setShow(true);
        setShow(false);
      }, 5000);
    }
  }
  return (
    <section id='about' className='about'>
      <div className='about-text'>
        <div className='about-text-grid'>
          <h1>12   </h1>
          <p className='fs-300 fw-bold'>    812                 ,    .             .       !
            <br /><br />
                            ,     .           -  ,     ,          .</p>
        </div>
      </div>

      <div className='about-img bg-pr-2'>
        <div className='about-img-grid'>
          <div>
            <img src={image2} alt="" />
          </div>
          <div className='about-img-double'>
            <img src={image3} alt="" />
            <img src={image4} alt="" />
          </div>
        </div>
      </div>

      <section id='call' onSubmit={handleSubmit} className='about-contact'>
        <h2 className='txt-lgh-tit'>  </h2>
        <p className='fs-300 txt-lgh-b'>   ,            </p>
        <form method='post' className='fs-300 about-contact-grid'>
          <input className='leave-req-name' type="text" name='name' placeholder='e ' onChange={handleChange} value={data.name} />
          <input className='leave-req-phone' type="phone" name='phone' placeholder='+7 (000) 000-0000' onChange={handleChange} value={data.phone} />
          <input type="email" name='email' placeholder='E-mail' onChange={handleChange} value={data.email} />
          <div className='ab-btn-con'>
            <button type='submit' className='bg-sec txt-lgh-tit'> </button></div>
        </form>
        {show ?
          <div id='reqConfirmed' className='req-conf fs-400 fw-bold txt-lgh-tit'>    {data.name}. <br />
                  </div> : null}
      </section>
    </section>
  )
}

export default About
